th.gridjs-th {
  border: 1px solid #e5e7eb;
  padding: 14px 24px;
  user-select: none;
  box-sizing: content-box;
  white-space: nowrap;
  outline: none; }
  th.gridjs-th-sort {
    cursor: pointer; }
  th.gridjs-th-sort:hover {
    background-color: #e5e7eb; }
  th.gridjs-th-sort:focus {
    background-color: #e5e7eb; }

th.gridjs-th:first-child {
  border-left: none; }

th.gridjs-th:last-child {
  border-right: none; }
